@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap');
a{
  color: black!important;
}
.nav-links{
  color: black;
  font-size: large;
}
.login-form{
  border-radius:10%;
}

@media only screen and (min-width: 601px) {
  .brand-logo {
    margin-left: 20px;
  }
}

@media only screen and (max-width: 600px) {
  .nav-wrapper {
    height: 64px;
  }

  .nav-wrapper .brand-logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  .sidenav-trigger {
    position: absolute;
    left: 10px;
    top: 16px;
  }

  .sidenav-trigger i {
    font-size: 32px;
  }

  .sidenav {
    width: 70%;
  }
}

.nav-links-login{
  size: 2rem;
}
.mycard{
  margin-top:5% ;
}
.auth-card{
  padding: 1.5rem;
  text-align: center;
  max-width: 600px;
  margin:10px auto;
}
.input-field:focus{
  border-bottom: 1px solid rgb(8, 93, 252)!important;
  box-shadow: 0 1px 0 0  rgb(8, 93, 252)!important;
}
.gallery{
  display: flex;
  gap:1rem;
  flex-wrap: wrap;
  justify-content: space-around;

}
.create-post-container{
  align-items: center;
}
.create-post-div{
  max-width: 60%;

}
.home-card{
  max-width: 600px;
  height: 100%;
  margin: 26px auto;
}
.card-image img{
width:400px;
height: 450px;
object-fit: cover;
}
.proflie-main-div{
  max-width: 70%;
  margin: 2% auto;
  
}
.main-div{
  justify-content: center;
  align-items: center;
  padding: 0 5%;
  margin: 2%;

}
@media(max-width:700px){
  .proflie-main-div{
    max-width: 100%;
    justify-content: space-around;
    
  }
  .main-div{
    justify-content: center;
    padding: 0;
  }

  .create-post-div{
    max-width: 90%;
  }
.text-data h4{
  font-size:1.2rem;
  font-weight: 500;
}
  .auth-card{
    max-width: 80%;
  }
  .card-image img{
    width:350px;
    height: 350px;
    object-fit: cover;
    }
}
.home-card{
  padding: 1rem;
width:95%;
}
.item{
  width: 30%;

}